@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap&subset=cyrillic");
@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap");
@tailwind base;
html {
  @apply font-body text-sm text-gray-800;
}

h1 {
  @apply text-4xl mb-5 font-light;
}

h2 {
  @apply text-3xl mb-4;
}

h3 {
  @apply text-2xl mb-3;
}

h4 {
  @apply uppercase mb-2 text-sm text-gray-600 font-bold tracking-wider;
}

a {
  @apply text-blue-500 transition;
}
a:hover {
  @apply text-blue-700;
}

img {
  height: auto;
  max-width: 100%;
}

button, input, optgroup, select, textarea {
  @apply text-base;
}

pre {
  @apply text-sm font-mono text-gray-900 whitespace-pre-wrap rounded p-8 bg-gray-200 overflow-x-auto transition;
}
pre:hover {
  @apply shadow-lg;
}

code {
  @apply text-sm font-mono rounded inline-block;
}

@tailwind components;
@tailwind utilities;
@tailwind screens;
